.pager {
  position: fixed;
  z-index: 999999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.next, .play-pause {
  position: fixed;
  z-index: 999999;
  bottom: 0;
  width: 80px;
  height: 80px;
}

.next {
  right: 0;
}

.play-pause {
  left: 0;
}


