.profile {
  overflow: hidden;
}

h1.first-title {
  text-shadow: 0 0 2px #000;
}

.first-title.badge {
  text-shadow: 0 0 1px #333;
  font-size: 1rem;
}

.class-record-title {
  color: #999;
  text-shadow: 1px 1px 1px #666;
}

.abilities-wrapper {
  height: 360px;
  padding: 30px 0;
  text-align: center;
}

.leetcode-wrapper>*+* {
  margin-left: 0.6rem;
}

.leetcode {
  font-size: 1.1rem;
  text-shadow: 0 0 1px #000;
}

.easy.badge {
  color: #fff;
  background-color: #5cb85c;
}

.medium.badge {
  color: #fff;
  background-color: #f0ad4e;
}

.hard.badge {
  color: #fff;
  background-color: #d9534f;
}

.class-record-wrapper {
  max-height: calc(100vh - 210px);
}


